.logo {
    /* width: 60%; */
    width: 132px;
    height: 48px;
    /* margin-top: 5%; */
    margin-top: 32px;
    /* margin-left: 10%; */
    margin-left: 48px;
    /* margin-bottom: 30%; */
}
.container {
    display: flex;
    flex-direction: column;
    align-items: start;    
    width: 100%;
    height: 100%;
    /* background-color: #000227; */
    /* background-color: #FFFFFF; */
    background-color: var(--main-background-color);
}
.btn {
    /* margin-top: 4%; */
    width: 100%;
    color: var(--main-background-color);
    border-color: var(--main-background-color);
    background-color: var(--btn-background-color);
    font-family: Inter, serif;
    font-weight: 400;
}
.btn span {
    text-align: left;
}

.chatsItems {
    height: 100%;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    margin-left: 10%;
}

.chatsItem {
    display: flex;
    flex-direction: row;
    margin-top: 5%;
    margin-bottom: 6%;
}

.chatItemBtn {
    width: 100%;
    color: var(--sidebar-text-color);
    margin-top: 2%;
    margin-bottom: 2%;
}

.chatItemBtn span {
    text-align: left;
}

.icons {
    color: var(--main-background-color);
}